.head {
    background-color: black;
    display: flex;
    top: 0;
    color: white;
    position: fixed;
    z-index: 999;
    opacity: 0.725;
    box-sizing: border-box;
    width: 100%;
}


.head-middle>nav>ul>li {
    display: inline-block;
    margin: 0 0 0 70px;
}


.head-right>nav>ul>li {
    display: inline-block;
    margin-left: 40px;
}

.main-logo {
    height: 40px;
    max-height: 40px;
}

.head-left {
    margin: 10px 0 10px 10px;
}

.head-middle {
    margin: 5px 0 0 0px;
}


#dwnlod {
    height: 22px;
    max-height: 22px;
    vertical-align: middle;
}

.menu-logo {
    height: 15px;
    max-height: 15px;
}


.r-menu-logo {
    height: 22px;
    max-height: 22px;
    vertical-align: middle;
}


.r-text {
    margin-left: 10px;
    font-size: 14px;
}


.head-right {
    margin: 3px 0 0 5.25vw;
}